import { UserBuildConditionals } from '@stencil/core/internal/stencil-public-runtime';
import { common, CommonData } from "../../global/data/common";
import { commonDynamic, CommonDynamicData } from "../../global/data/commonDynamic";

import { DataLayer } from '../dataLayer';

export function loadCommonData(commonDataId: string, commonDynamicId: string, Build: UserBuildConditionals) {
    const commonDataElement = document.getElementById(commonDataId);
    const commonData = JSON.parse(commonDataElement.innerHTML) as CommonData;

    Object.keys(commonData).map(key => {
        common.set(key as keyof CommonData, commonData[key]);
    });

    if (Build.isBrowser) {
        const commonDynamicDataElement = document.getElementById(commonDynamicId);
        const commonDynamicData = JSON.parse(commonDynamicDataElement.innerHTML) as CommonDynamicData;

        Object.keys(commonDynamicData).map(key => {
            commonDynamic.set(key as keyof CommonDynamicData, commonDynamicData[key]);
        });

        setTimeout(() => {
            commonDynamic.set("loaded", true);

            const tracking = commonDynamic.get("tracking");
            DataLayer.pageview(tracking.pageview);
        }, 100);
    }
}